@import '../../modal/css/global';

[data-component='social-feed'],
.social-feed-wrapper,
.social-feed-wrapper__overlay {
    height: 100%;
}

.social-feed {
    @include pnp-svg-icon-classes;
    display: flex;
    flex-direction: column;
    height: 100%;
    overflow: scroll;
    background: $grey-10;
    border: 1px solid $grey-20;

    &__header {
        @include pnp-typeface(text-16, no-margin);
        display: flex;
        align-items: center;
        justify-content: space-between;
        min-height: 44px;
        padding: 4px 13px;
        color: $grey-70;
        background: $white;
    }
}

.social-card {
    @include pnp-standard-box-shadow;
    position: relative;
    padding: 12px;
    background: $white;
    border-radius: 4px;

    &__open-modal {
        @include pnp-reset-button;
        outline-offset: 12px;
    }

    &__view-external {
        position: absolute;
        top: 12px;
        right: 12px;
        color: $primary-a;

        &--logo-twitter {
            color: $social-twitter;
        }

        &--logo-facebook {
            color: $social-facebook;
        }

        &--logo-youtube {
            color: $social-youtube;
        }
    }
}

.social-card,
.social-card-modal {
    @include pnp-typeface(text-14, no-margin);

    margin: 12px;
    margin-bottom: 8px;

    &__header {
        display: flex;
        align-items: center;
    }

    &__header-image {
        width: 36px;
        height: 36px;
        overflow: hidden;
        border: 1px solid rgba(0, 0, 0, 0.1);
        border-radius: 50%;
    }

    &__title {
        margin-left: 12px;
    }

    &__post-created {
        color: $grey-70;
    }

    &__account-name {
        @include pnp-typeface(text-14-bold, no-margin);
    }

    &__body {
        margin-top: 8px;
    }

    &__show-more {
        @include pnp-reset-button;
        text-decoration: underline;
    }

    &__body-image {
        display: block;
        width: 100%;
        margin-top: 12px;
        overflow: hidden;
        border-radius: 0.25rem;
    }
}

.social-card-modal {
    @include pnp-svg-icon-classes;
    max-width: 450px;
    margin: auto;

    &__content {
        border-radius: 4px;
    }

    &__header {
        padding: 24px;
        border-bottom: 1px solid $grey-20;
    }

    &__close-icon {
        @include pnp-reset-button;
        position: absolute;
        top: 12px;
        right: 12px;
    }

    &__body {
        margin: 0;
        padding: 24px;
    }

    &__video-embed-wrapper {
        @include pnp-ratio16by9-wrapper();
        margin-top: 12px;
    }

    &__video-embed {
        @include pnp-ratio16by9-inner();
    }

    &__footer {
        display: flex;
        justify-content: flex-end;
        padding: 24px;
        border-top: 1px solid $grey-20;
    }

    &__open-external-button {
        @include pnp-button;
    }
}
