    text-18: (
        font-size: 18px,
        line-height: 26px,
        margin-bottom: 12px,
        font-weight: 400,
    ),
    text-16: (
        font-size: 16px,
        line-height: 24px,
        margin-bottom: 12px,
        font-weight: 400,
    ),
    text-14: (
        font-size: 14px,
        line-height: 18px,
        margin-bottom: 8px,
        font-weight: 400,
    ),
    text-24-bold: (
        font-size: 24px,
        line-height: 32px,
        margin-bottom: 16px,
        font-weight: 700,
    ),
    text-18-bold: (
        font-size: 18px,
        line-height: 26px,
        margin-bottom: 12px,
        font-weight: 700,
    ),
    text-16-bold: (
        font-size: 16px,
        line-height: 24px,
        margin-bottom: 12px,
        font-weight: 700,
    ),
    text-14-bold: (
        font-size: 14px,
        line-height: 18px,
        margin-bottom: 8px,
        font-weight: 700,
    ),
    heading-64: (
        font-size: 64px,
        line-height: 84px,
        margin-bottom: 48px,
        font-weight: 400,
    ),
    heading-48: (
        font-size: 48px,
        line-height: 64px,
        margin-bottom: 32px,
        font-weight: 400,
    ),
    heading-32: (
        font-size: 32px,
        line-height: 44px,
        margin-bottom: 16px,
        font-weight: 400,
    ),
    heading-26: (
        font-size: 26px,
        line-height: 36px,
        margin-bottom: 16px,
        font-weight: 400,
    ),
    heading-20: (
        font-size: 20px,
        line-height: 24px,
        margin-bottom: 16px,
        font-weight: 400,
    ),
    heading-18: (
        font-size: 18px,
        line-height: 26px,
        margin-bottom: 16px,
        font-weight: 400,
    ),
    heading-64-bold: (
        font-size: 64px,
        line-height: 84px,
        margin-bottom: 48px,
        font-weight: 700,
    ),
    heading-48-bold: (
        font-size: 48px,
        line-height: 64px,
        margin-bottom: 32px,
        font-weight: 700,
    ),
    heading-32-bold: (
        font-size: 32px,
        line-height: 44px,
        margin-bottom: 16px,
        font-weight: 700,
    ),
    heading-26-bold: (
        font-size: 26px,
        line-height: 36px,
        margin-bottom: 16px,
        font-weight: 700,
    ),
    heading-20-bold: (
        font-size: 20px,
        line-height: 24px,
        margin-bottom: 16px,
        font-weight: 700,
    ),
    heading-18-bold: (
        font-size: 18px,
        line-height: 26px,
        margin-bottom: 16px,
        font-weight: 700,
    ),
);

// Default transition values
$pnp-default-transition-type: ease !default;
$pnp-default-transition-duration: 0.5s !default;
$pnp-default-scale-ratio: 1.05 !default;

$pnp-ease-in-out: cubic-bezier(0.645, 0.045, 0.355, 1) !default;

$pnp-button-transition: color 0.15s ease-in-out,
    background-color 0.15s ease-in-out, border-color 0.15s ease-in-out,
    box-shadow 0.15s ease-in-out !default;

// Edify specs
$edify-card-padding: 12px;
$edify-color-light: $grey-10;
$edify-color-dark: $grey-70;

$edify-color-yellow: rgb(255, 213, 32);
